import React from "react";
import { getAuth, signOut } from "firebase/auth";
import { useNavigate } from "react-router-dom";

//  
function SignOut() {
  const navigate = useNavigate();

  const auth = getAuth();
  const out = () => {
    signOut(auth)
      .then(() => {
        navigate("/login");
      })
      .catch((error) => {
        console.log(error);
      });
  };

  return (
    <div>
      <h4> </h4>
      <button onClick={() => out()}></button>
    </div>
  );
}

export default SignOut;
