import React, { useEffect, useState } from "react";
import { useDispatch, useSelector } from "react-redux";
import { setLang, setIdWord } from "../redux/slices/wordsSlice";
import data from "../words.json";

function MyWords() {
    const dispatch = useDispatch();
    const { lang, idWord, words } = useSelector((state) => state.words);
  
    const [selectedDate, setSelectedDate] = useState("");
  
    console.log('selected',selectedDate);
  
    const filteredWords = words.filter((word) => word.dateAdded === selectedDate);
  
    console.log(filteredWords);
  
    const addIdWord = () => {
      if (idWord < filteredWords.length - 1) {
        dispatch(setIdWord(idWord + 1));
      } else {
        dispatch(setIdWord(0));
      }
    };
  
    const handleChangeDate = (event) => {
        const date = new Date(event.target.value);
        const formattedDate = `${date.getDate() < 10 ? '0' + date.getDate() : date.getDate()}.${date.getMonth() + 1 < 10 ? '0' + (date.getMonth() + 1) : date.getMonth() + 1}.${date.getFullYear()}`;
        setSelectedDate(formattedDate);
      };
      
  
    return (
      <div className="my-words">
        <h1> </h1>
        <div>
          <label htmlFor="date">
                   , <br></br>  
               .<br></br> ,   
             ,         ,{" "}
            <br></br>    .     
            , ,    .{" "}
          </label>
          <input
  type="date"
  id="date"
  value={selectedDate.split(".").reverse().join("-")}
  onChange={handleChangeDate}
/>
<div>Selected date: {selectedDate}</div>
        </div>
        {lang === true ? (
          <div className="wrapper">
            <div className="word" onClick={() => dispatch(setLang(!lang))}>
              {filteredWords[idWord] &&
                data &&
                data.result.filter(
                  (item) => item.id === filteredWords[idWord].id
                )[0].englishWord}
            </div>
            <div className="trans">
              [{" "}
              {filteredWords[idWord] &&
                data &&
                data.result.filter(
                  (item) => item.id === filteredWords[idWord].id
                )[0].englishPronunciation}
              ]{" "}
            </div>
          </div>
        ) : (
          <div className="wrapper">
            <div className="word" onClick={() => dispatch(setLang(!lang))}>
              {filteredWords[idWord] &&
                data &&
                data.result.filter(
                  (item) => item.id === filteredWords[idWord].id
                )[0].translateWord}
            </div>
          </div>
        )}
        {/*  <button onClick={() => dispatch(setLang(!lang))}></button> */}
        <button onClick={() => addIdWord()}> </button>
        
      </div>
    );
  }
  
  export default MyWords
  
